<template>
  <el-button @click="goBack">返回配置详情</el-button>
  <el-button @click="getInstanceCfgInfo" :loading="flushLoading">刷新页面</el-button>
  <span style="margin-left: 20px;"><el-text type="primary">配置：{{ this.username }} —— 编辑实例配置</el-text></span>
  <el-divider class="divider"/>
  <div class="instance-config-container" v-loading="flushLoading">
    <el-row :gutter="20" align="stretch">
      <el-col :span="12">
        <!-- 实例名称 -->
        <el-card class="config-card">
          <div>修改实例名称</div>
          <el-row :gutter="20" style="margin-top: 10px">
            <el-col :span="16">
              <el-input
                  v-model="instanceCfg.instanceName" clearable
                  placeholder="请输入实例名称"
              ></el-input>
            </el-col>
            <el-col :span="8">
              <el-button type="primary" @click="modifyInstanceName()" :loading="nameLoading" block>修改实例名称
              </el-button>
            </el-col>
          </el-row>
        </el-card>

        <!-- 实例CPU和内存大小 -->
        <el-card class="config-card">
          <div>修改CPU和内存</div>
          <el-row :gutter="20" align="middle" style="margin-top: 10px">
            <el-col :span="8">
              <el-input-number
                  v-model="instanceCfg.ocpus"
                  :min="1"
                  label="CPU大小"
              ></el-input-number>
              核
            </el-col>
            <el-col :span="8">
              <el-input-number
                  v-model="instanceCfg.memory"
                  :min="1"
                  label="内存大小"
              ></el-input-number>
              GB
            </el-col>
            <el-col :span="8">
              <el-button type="primary" @click="modifyCpuAndMemory()" :loading="cpuLoading" block>修改CPU和内存
              </el-button>
            </el-col>
          </el-row>
        </el-card>

        <!-- 自动救援 -->
        <el-card class="config-card">
          <div>自动救援/缩小硬盘（默认47GB）</div>
          <el-row :gutter="20" align="middle" style="margin-top: 10px">
            <el-col :span="16">
              <el-tooltip content="不占用引导卷额度，用于救援失败后恢复" placement="bottom">
                <el-checkbox v-model="keepBackupVolume">保留原引导卷的备份卷
                </el-checkbox>
              </el-tooltip>
            </el-col>
            <el-col :span="8">
              <el-button type="primary" @click="autoRescue()" block>开始救援/缩小硬盘</el-button>
            </el-col>
          </el-row>
        </el-card>
      </el-col>

      <el-col :span="12">
        <!-- 实例引导卷大小 -->
        <el-card class="config-card">
          <div>修改引导卷大小及VPU</div>
          <el-row :gutter="20" align="middle" style="margin-top: 10px">
            <el-col :span="8">
              <el-input-number
                  v-model="instanceCfg.bootVolumeSize"
                  :min="50"
                  label="引导卷大小"
              ></el-input-number>
              GB
            </el-col>
            <el-col :span="8">
              <el-slider
                  v-model="instanceCfg.bootVolumeVpu"
                  :min="10"
                  :max="120"
                  :step="10"
                  label="引导卷VPU大小"
                  show-tooltip
              ></el-slider>
              <span>VPU: {{ instanceCfg.bootVolumeVpu }}</span>
            </el-col>
            <el-col :span="8">
              <el-button type="primary" @click="modifyBootVolume()" :loading="bootVolumeLoading" block>修改引导卷
              </el-button>
            </el-col>
          </el-row>
        </el-card>

        <!-- 实例IPV6地址 -->
        <el-card class="config-card">
          <div>实例IPV6地址</div>
          <el-row :gutter="20" align="middle" style="margin-top: 10px">
            <el-col :span="16">
              <el-input
                  v-model="instanceCfg.ipv6"
                  placeholder="实例IPV6地址"
                  :disabled="true"
              ></el-input>
            </el-col>
            <el-col :span="8">
              <el-button
                  type="primary"
                  @click="createIpv6()"
                  :disabled="!!instanceCfg.ipv6"
                  block
                  :loading="ipv6Loading"
              >附加IPV6
              </el-button>
            </el-col>
          </el-row>
        </el-card>

        <!-- 一键开启500MB -->
        <el-card class="config-card">
          <div>一键开启500MB/更换500MB实例公网IP</div>
          <el-row :gutter="20" align="middle" style="margin-top: 10px">
            <el-col :span="16">
              该实例SSH端口：
              <el-input-number
                  v-model="oneClick500mParams.sshPort"
                  label="SSH端口"
              >
              </el-input-number>
            </el-col>
            <el-col :span="8">
              <el-button type="primary" @click="this.oneClick500M()" block>一键开启500MB</el-button>
            </el-col>
          </el-row>
        </el-card>


      </el-col>
    </el-row>
  </div>
</template>

<script>
import {ElMessage, ElMessageBox} from "element-plus";

export default {
  data() {
    return {
      username: '',
      instanceId: '',
      ociCfgId: '',
      instanceCfg: {
        instanceName: '',
        ipv6: '',
        ocpus: 1,
        memory: 1,
        bootVolumeSize: 50,
        bootVolumeVpu: 10,
      },
      oneClick500mParams: {
        ociCfgId: '',
        instanceId: '',
        sshPort: 22
      },
      flushLoading: false,
      nameLoading: false,
      cpuLoading: false,
      bootVolumeLoading: false,
      ipv6Loading: false,
      keepBackupVolume: false,

    };
  },
  mounted() {
    this.username = this.$route.query.username;
    this.instanceId = this.$route.query.ocId;
    this.ociCfgId = this.$route.query.ociCfgId;
    this.oneClick500mParams.ociCfgId = this.$route.query.ociCfgId;
    this.oneClick500mParams.instanceId = this.$route.query.ocId;
    this.getInstanceCfgInfo()
  },
  methods: {
    goBack() {
      this.$router.back(); // 返回上一个页面
    },
    async getInstanceCfgInfo() {
      this.flushLoading = true
      await this.$axios.post('/oci/getInstanceCfgInfo', {ociCfgId: this.ociCfgId, instanceId: this.instanceId})
          .then(response => {
            if (response.data.success) {
              this.instanceCfg = response.data.data
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.flushLoading = false
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async oneClick500M() {
      try {
        let confirmText = '确定要一键开启500MB吗？⚠️仅试用期账号或升级号（付费）才可以开启，且只能有一台免费AMD实例开启'
        // 弹框确认
        const confirmResult = await ElMessageBox.confirm(
            confirmText, // 弹框内容
            '一键开启500MB操作', // 弹框标题
            {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning', // 弹框类型
            }
        );

        if (confirmResult === 'confirm') {
          this.$axios.post('/oci/oneClick500M', {...this.oneClick500mParams})
              .then(response => {
                if (response.data.success) {
                  ElMessage({
                    message: response.data.msg,
                    type: 'success',
                    duration: 2000 // 设置提示显示时间，单位是毫秒
                  });
                  // 跳转到日志页
                  this.$router.push(`/dashboard/ociLog`);
                } else {
                  ElMessage({
                    message: response.data.msg,
                    type: 'error',
                    duration: 2000 // 设置提示显示时间，单位是毫秒
                  });
                }
              })
              .catch(error => {
                console.error('Error:', error)
              })
        }
      } catch (cancelError) {
        // 用户点击了 "取消" 或关闭了弹框，不需要执行任何操作
        ElMessage({
          message: '操作已取消',
          type: 'info',
          duration: 2000,
        });
        console.error('cancelError:', cancelError)
      }
    },
    async autoRescue() {
      try {
        let confirmText = '确定要开始救援/缩小硬盘吗？ ⚠️ 请确保删除掉原引导卷后，至少还剩余50GB引导卷额度！自动救援不一定百分百成功，如果中途失败，自行登录 Oracle Cloud 官方面板，将备份卷恢复成引导卷并附加回原实例。'
        if (this.keepBackupVolume) {
          confirmText += "🔔 当前选择了保留原引导卷的备份卷，救援结束后需要自行删除备份卷，否则会永久保留，且占用备份卷额度。"
        }
        // 弹框确认
        const confirmResult = await ElMessageBox.confirm(
            confirmText, // 弹框内容
            '自动救援/缩小硬盘操作', // 弹框标题
            {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning', // 弹框类型
            }
        );

        if (confirmResult === 'confirm') {
          this.$axios.post('/oci/autoRescue',
              {
                ociCfgId: this.ociCfgId,
                instanceId: this.instanceId,
                name: this.instanceCfg.instanceName,
                keepBackupVolume: this.keepBackupVolume,
              }
          )
              .then(response => {
                if (response.data.success) {
                  ElMessage({
                    message: '自动救援/缩小硬盘任务已下发，具体流程请查看日志',
                    type: 'success',
                    duration: 2000 // 设置提示显示时间，单位是毫秒
                  });
                  // 跳转到日志页
                  this.$router.push(`/dashboard/ociLog`);
                } else {
                  ElMessage({
                    message: response.data.msg,
                    type: 'error',
                    duration: 2000 // 设置提示显示时间，单位是毫秒
                  });
                }
              })
              .catch(error => {
                console.error('Error:', error)
              })
        }
      } catch (cancelError) {
        // 用户点击了 "取消" 或关闭了弹框，不需要执行任何操作
        ElMessage({
          message: '操作已取消',
          type: 'info',
          duration: 2000,
        });
      }
    },
    async modifyInstanceName() {
      try {
        // 弹框确认
        const confirmResult = await ElMessageBox.confirm(
            '确定要修改实例名称吗？', // 弹框内容
            '修改实例名称操作', // 弹框标题
            {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning', // 弹框类型
            }
        );

        if (confirmResult === 'confirm') {
          this.nameLoading = true

          await this.$axios.post('/oci/updateInstanceName',
              {
                ociCfgId: this.ociCfgId,
                instanceId: this.instanceId,
                name: this.instanceCfg.instanceName
              }
          )
              .then(response => {
                if (response.data.success) {
                  ElMessage({
                    message: response.data.msg,
                    type: 'success',
                    duration: 2000 // 设置提示显示时间，单位是毫秒
                  });
                  // this.getInstanceCfgInfo()
                  this.nameLoading = false
                } else {
                  ElMessage({
                    message: response.data.msg,
                    type: 'error',
                    duration: 2000 // 设置提示显示时间，单位是毫秒
                  });
                }
              })
              .catch(error => {
                console.error('Error:', error)
              })
        }
      } catch (cancelError) {
        // 用户点击了 "取消" 或关闭了弹框，不需要执行任何操作
        ElMessage({
          message: '操作已取消',
          type: 'info',
          duration: 2000,
        });
      }
    },
    async modifyCpuAndMemory() {
      try {
        // 弹框确认
        const confirmResult = await ElMessageBox.confirm(
            '修改实例CPU和内存会重启实例，确定要修改吗？', // 弹框内容
            '修改实例CPU和内存操作', // 弹框标题
            {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning', // 弹框类型
            }
        );

        if (confirmResult === 'confirm') {
          this.cpuLoading = true

          await this.$axios.post('/oci/updateInstanceCfg',
              {
                ociCfgId: this.ociCfgId,
                instanceId: this.instanceId,
                ocpus: this.instanceCfg.ocpus,
                memory: this.instanceCfg.memory
              }
          )
              .then(response => {
                if (response.data.success) {
                  ElMessage({
                    message: response.data.msg,
                    type: 'success',
                    duration: 2000 // 设置提示显示时间，单位是毫秒
                  });
                  // this.getInstanceCfgInfo()
                  this.cpuLoading = false
                } else {
                  ElMessage({
                    message: response.data.msg,
                    type: 'error',
                    duration: 2000 // 设置提示显示时间，单位是毫秒
                  });
                }
              })
              .catch(error => {
                console.error('Error:', error)
              })
        }
      } catch (cancelError) {
        // 用户点击了 "取消" 或关闭了弹框，不需要执行任何操作
        ElMessage({
          message: '操作已取消',
          type: 'info',
          duration: 2000,
        });
      }
    },
    async modifyBootVolume() {
      try {
        // 弹框确认
        const confirmResult = await ElMessageBox.confirm(
            '确定要修改实例引导卷吗？', // 弹框内容
            '修改实例引导卷操作', // 弹框标题
            {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning', // 弹框类型
            }
        );

        if (confirmResult === 'confirm') {
          this.bootVolumeLoading = true
          await this.$axios.post('/oci/updateBootVolumeCfg',
              {
                ociCfgId: this.ociCfgId,
                instanceId: this.instanceId,
                bootVolumeSize: this.instanceCfg.bootVolumeSize,
                bootVolumeVpu: this.instanceCfg.bootVolumeVpu
              }
          )
              .then(response => {
                if (response.data.success) {
                  ElMessage({
                    message: response.data.msg,
                    type: 'success',
                    duration: 2000 // 设置提示显示时间，单位是毫秒
                  });
                  // this.getInstanceCfgInfo()
                  this.bootVolumeLoading = false
                } else {
                  ElMessage({
                    message: response.data.msg,
                    type: 'error',
                    duration: 2000 // 设置提示显示时间，单位是毫秒
                  });
                }
              })
              .catch(error => {
                console.error('Error:', error)
              })
        }
      } catch (cancelError) {
        // 用户点击了 "取消" 或关闭了弹框，不需要执行任何操作
        ElMessage({
          message: '操作已取消',
          type: 'info',
          duration: 2000,
        });
      }
    },
    async createIpv6() {
      try {
        // 弹框确认
        const confirmResult = await ElMessageBox.confirm(
            '确定要为实例附加 IPV6 吗？', // 弹框内容
            '附加 IPV6 操作', // 弹框标题
            {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning', // 弹框类型
            }
        );

        if (confirmResult === 'confirm') {
          this.ipv6Loading = true
          await this.$axios.post('/oci/createIpv6',
              {
                ociCfgId: this.ociCfgId,
                instanceId: this.instanceId,
              }
          )
              .then(response => {
                if (response.data.success) {
                  ElMessage({
                    message: response.data.msg,
                    type: 'success',
                    duration: 2000 // 设置提示显示时间，单位是毫秒
                  });
                  this.ipv6Loading = false
                } else {
                  ElMessage({
                    message: response.data.msg,
                    type: 'error',
                    duration: 2000 // 设置提示显示时间，单位是毫秒
                  });
                }
              })
              .catch(error => {
                console.error('Error:', error)
              })
              .finally(() => {
                this.getInstanceCfgInfo()
              })
        }
      } catch (cancelError) {
        // 用户点击了 "取消" 或关闭了弹框，不需要执行任何操作
        ElMessage({
          message: '操作已取消',
          type: 'info',
          duration: 2000,
        });
      }
    },
  },
};
</script>

<style>
.instance-config-container {
  max-width: 1800px;
  margin: 0 auto;
}

.config-card {
  margin-bottom: 20px;
  border: 1px solid #ebeef5;
  border-radius: 8px;
  height: 30%; /* 关键：让卡片自动填充高度 */
  display: flex;
  flex-direction: column; /* 保持内部布局正常 */
}
</style>
